import React from "react";
import ReactTable from "react-table";
import { makeData, Logo, Tips } from "./Utils.js";
import "react-table/react-table.css";

class RetailerLoyaltyTable extends React.Component {
    constructor() {
      super();
      this.state = {
        data: makeData()
      };
    }
    componentDidMount() {
        //todo: temp static param - fetch from user
      fetch('https://playhelloworld.azurewebsites.net/api/retailerloyalty/1')
        .then(response => response.json())
        .then(json => {
          console.log(json);
          this.setState({ data: json });
        });        
    }
    render() {
      const { data } = this.state;
      return (
        <div>
          <ReactTable
            data={data}
            columns={[
              {
                Header: "Name",
				        accessor: "name"                                
              },
              {
                Header: "logo",
                accessor: "logo"
              },
              {
                Header: "description",
                accessor: "description"
              }             
            ]}
            defaultPageSize={10}
            className="-striped -highlight"
          />
          <br />
          <Tips />
          <Logo />
        </div>
      );
    }
  }

  export default RetailerLoyaltyTable;



