import React from "react";
import ReactTable from "react-table";
import { makeData, Logo, Tips } from "./Utils.js";
import "react-table/react-table.css";
import InputText from "./common.js"

const Stores = (props) => {
    return <div>
    <ReactTable
      data={props.data}
      columns={[
        {
          Header: "name",
          accessor: "name"
        },
        {
          Header: "phone",
          accessor: "phone"
        }             
      ]}
      defaultPageSize={10}
      className="-striped -highlight"
    />
    <br />    
  </div>
}

//פרטי עסק
export default class RetailerInfo extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            web: null,
            email:null,
            facebook:null,
            stores: []
        }
        this.handleweb = this.handleweb.bind(this);    
        this.handleemail = this.handleemail.bind(this);    
        this.handlefacebook = this.handlefacebook.bind(this);    
    }
    componentDidMount(){
        //get retailer details
        fetch(`https://playhelloworld.azurewebsites.net/api/retailerinfo`)
            .then(res => res.json())
            .then(res => {
                console.dir(res);
            });

        fetch(`https://playhelloworld.azurewebsites.net/api/retailerstores`)
            .then(res => res.json())
            .then(res => {
                this.setState({ stores: res });
                console.dir(res);
            });
    }
    handleemail(val){
        this.setState({web: val});
    }
    handleweb(val){
        this.setState({email: val});
    }
    handlefacebook(val){
        this.setState({facebook: val});
    }
    handleSubmit(e) {
        e.preventDefault();
        console.info('handleSubmit;');
    }
    render(){
        return <div>
            <form>
            <div className="form-row">
                <div className="form-group col-md-4" > <InputText text="כתובת ברשת"  id="name" onTextChange={this.handleweb} class="form-control" value={this.state.web} /> </div>
                <div className="form-group col-md-4" > <InputText text="דואל"  id="name" onTextChange={this.handleemail} class="form-control" value={this.state.email} /> </div>
                <div className="form-group col-md-4" > <InputText text="כתובת facebook"   onTextChange={this.handlefacebook} class="form-control" value={this.state.facebook} /> </div>
            </div>
            <div>
                <input className="btn btn-primary"  type="submit" value="עדכן" />
            </div>
            </form>
            <div>
                <Stores data={this.state.stores} />    
            </div>
            
        </div>
    }
}



